@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  :root {
    --primaryColor: #af9a7d;
    --mainWhite: #fff;
    --offWhite: #f7f7f7;
    --mainBlack: #222;
    --mainRed: #d23669;
    --darkGrey: #cfcfcf;
    --mainTransition: all 0.3s linear;
    --mainSpacing: 3px;
    --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
    --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  }
  /* globals */
  body {
    font-size: 1.2rem;
    padding-top: 16px;
    color: var(--mainBlack);
    background: var(--mainWhite);
    font-family: 'inconsolata', Verdana, Geneva, Tahoma, sans-serif;
    line-height: 1.2;
  }
  h1 {
    font-size: 3em;
    line-height: 1;
    margin-bottom: 0.5em;
  }

  span{
      color: var(--mainRed);
      font-weight: bold;
  }

  a{
    text-decoration: none;
  }

  p{
    padding-bottom: 1.4rem;
    line-height: 1.5;
  }

  .col{
    display: flex;
    /* margin: auto; */
    margin: 5vh 10vw;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: left;
    max-width: 40rem
  }

  .menu{
      display: flex;
      
  }

  .menu h2 span{

    font-weight: lighter;
    padding-right: 2rem;
  }

  ul{
      
      display: flex;
      flex-direction: row;
  }

  .home-list li{
    padding-right: 2rem;
  }

  ul.projects-list {
    display: flex;
    flex-direction: column;
    line-height: 0%;
    padding-top: .5rem;
  }


  @media screen and (max-width: 600px) {
    ul{
      display: flex;
      flex-direction: column;
    }
    li{
      /* padding: .5rem; */
    }
  }